import VueGoodTable from 'vue-good-table';
import 'vue-good-table/dist/vue-good-table.css';
Vue.use(VueGoodTable);

Vue.config.devtools = process.env.APP_MODE == 'development';

new Vue({
  name: 'app',
  el: '#app',
  render: h => h(App)
});